body{
	margin: 0;
	padding: 0;
    @include animationTransition;
	font-family: 'Archivo', sans-serif;
  }


*, *:before, *:after { box-sizing: border-box; }
  
h1, h2, h3, h4, h5, h6 {
    margin: 0;
    color: #000;
}
p{
    margin: 0;
    color: #000;
    font-size: 16px;
}


ul, li {
margin: 0;
padding: 0;
list-style-type: none;
}

a {
	text-decoration: none;
	transition: all .3s ease-in-out;
	color: #000;
	&:hover{
		text-decoration: none;
	}
}

.clearfix {
	&:before,
	&:after {
    display: table;
    content: ' '
	}
	&:after {
		clear: both;
	}
}

.viewport {
    min-height: 100vh;
    overflow: hidden;
    position: relative;
}


.section-container{
	margin: 0 auto;
	width: 100%;
	max-width: 992px;
}